.header {

    padding: 0 0 0 50px;
    position: fixed;
    left: 50px;
    right: 50px;
    top: 40px;
    height: 80px;
    z-index: 100;
    background: #585d65;
    border-radius: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .nav {
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
}

